import React, { Component } from 'react';
import { Row, Col, Card, Divider, Table } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { setListStudentForSummarizePoints } from 'dump/data';

class TableListSummarizeSemester extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'placeOfBirth',
        key: 'placeOfBirth',
      },
      {
        title: 'TBCHT4',
        dataIndex: 'TBCHT4',
        key: 'TBCHT4',
      },
      {
        title: 'TBCHC10',
        dataIndex: 'TBCHC10',
        key: 'TBCHC10',
      },
      {
        title: 'Xếp loại',
        dataIndex: 'rank',
        key: 'rank',
      },
      {
        title: 'Số HP nợ',
        dataIndex: 'SoHPNo',
        key: 'SoHPNo',
      },
      {
        title: 'Cơ học cơ sở',
        dataIndex: 'CoHocCoSo',
        key: 'CoHocCoSo',
      },
      {
        title: 'Đường lối cách mạng',
        dataIndex: 'DgLoiCachMang',
        key: 'DgLoiCachMang',
      },
      {
        title: 'Hình họa - Vẽ kỹ thuật',
        dataIndex: 'HHVeKyThuat',
        key: 'HHVeKyThuat',
      },
      {
        title: 'Những nguyên lý cơ bản',
        dataIndex: 'NNLCBCNMacLeNin',
        key: 'NNLCBCNMacLeNin',
      },
      {
        title: 'Sức bền vật liệu',
        dataIndex: 'SucBenVL',
        key: 'SucBenVL',
      },
      {
        title: 'Tư tưởng Hồ Chí Minh',
        dataIndex: 'TuTuongHCM',
        key: 'TuTuongHCM',
      },
      {
        title: 'Vật liệu xây dựng',
        dataIndex: 'VatLieuXD',
        key: 'VatLieuXD',
      },
      {
        title: 'Vẽ Auto Card',
        dataIndex: 'VeAutocard',
        key: 'VeAutocard',
      },
      // {
      //   title: 'Thao tác',
      //   dataIndex: 'operation',
      //   render: () => (
      //     <Button className="btnSmallDeleteInTable" onClick={this.showModal}>
      //       Xóa
      //     </Button>
      //   ),
      // },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <Card>
        <Divider>Tổng hợp kết quả học tập học kỳ</Divider>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <Row>
          <Col>
            <div>
              <Table
                bordered
                rowSelection={this.rowSelection}
                dataSource={setListStudentForSummarizePoints}
                columns={this.columns}
                scroll={{ x: 'true' }}
              />
            </div>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default TableListSummarizeSemester;



